ol li {
  list-style-type: none;
  text-align: left;
  margin-bottom: 10px;
  border: 2px solid #BF40BF;
  background-color: #bf40bf38;
  border-radius: 10px;
  padding-left: 10px;
  font-family: cursive;
  letter-spacing: 2px;
  width: 100%;


}

.listitem {
  margin-left: 10px;
  max-height: 300px;
  height: 300px;
  overflow: auto;

}

.head {
  background-color: black;
  color: white;
  padding-left: 50px;
  padding-right: 50px;
  margin-bottom: 20px;

}

input {
  height: 29px;
  border-radius: 18px;
  border: 1px solid black;
  padding: 16px;
  font-size: 21px;
  margin-top: 14px;
  background: rgba(255, 255, 255, 0.658);
}

input:focus {
  background-color: rgba(255, 255, 255, 0.389);

}

.flex {
  display: flex;
  gap: 5px;
}

.container {
  display: flex;
  gap: 10px;
}

