.listhistory {
  height: 400px;
  max-height: 400px;
  overflow: auto;
}

.main {
  display: flex;

}


/* CSS */
.btn {
  background: #C1B2E9;
  border: 1px solid #C1B2E9;
  border-radius: 6px;
  /* box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px; */
  /* box-sizing: border-box; */
  color: #FFFFFF;
  cursor: pointer;
  height: 27px;
  font-size: 22px;
  border-radius: 34px;
  margin-top: 18px;
  /* padding: 12px 14px; */
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  padding-right: 10px;
  padding-left: 10px;
}

.btn:hover,
.btn:active {}

.btn:active {

  color: white;
  background-color: greenyellow;
  border: 1px solid greenyellow;
}

.cancelbtn {
  background: #C1B2E9;
  border: 1px solid #C1B2E9;
  border-radius: 6px;
  /* box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px; */
  /* box-sizing: border-box; */
  color: #FFFFFF;
  cursor: pointer;
  height: 26px;
  font-size: 16px;
  border-radius: 34px;
  margin-top: 1px;
  /* padding: 12px 14px; */
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  padding-right: 10px;
  padding-left: 10px;
  margin-right: 10px;
}

.cancelbtn:active {

  color: white;
  background-color: rgb(223, 19, 19);
  border: 1px solid rgb(244, 14, 22);
}